@import url("https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 2.4rem;
  font-family: sans-serif;
  color: #312008;
  font-family: "Quicksand";
  font-weight: 500;
}

.App {
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

h1 {
  text-align: center;
  background-color: #f5ab3c;
  font-family: "Monoton";
  font-size: 8rem;
  text-transform: uppercase;
  font-weight: 400;
  word-spacing: 30px;
  letter-spacing: 1px;
  padding: 2.4rem 0;
}

.add-form {
  background-color: #fbdaa8;
  padding: 2.8rem 2.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}

h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

button,
select,
input {
  background-color: #fef6e9;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  font-size: 1.8rem;
  cursor: pointer;
}

.add-form button {
  text-transform: uppercase;
  background-color: #f6b551;
}
.add-form button:hover {
  background-color: #fce3be;
}

.list {
  background-color: #fbdaa8;
  color: #5a3e2b;
  padding: 4rem 0;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}

.actions button,
.list select {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
}

.list ul {
  list-style: none;
  width: 80%;
  overflow: scroll;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: center;
}

.list li {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;
}

input[type="checkbox"] {
  height: 2rem;
  width: 2rem;
  accent-color: #e5771f;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
  padding: 0.8rem;
  transform: translateY(2px);
}

.stats {
  background-color: #f6b551;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}

.all-packed {
  background-color: #00cc00;
}

@media (max-width: 1070px) {
  h1 {
    font-size: 4rem;
  }
}

@media (max-width: 750px) {
  .add-form {
    flex-direction: column;
  }

  .form input[type="text"],
  .form input[type="number"] {
    max-width: 100%;
  }

  h1 {
    font-size: 2.5rem;
    word-spacing: 5px;
    margin: 0 auto;
    width: 100%;
  }

  h3 {
    text-align: center;
  }
}
